import React,{Component} from 'react'
import { Form, Button, message, Radio, Spin } from 'antd';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;

export class DetailForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            name: "",
            gender: "",
            nickName: "",
            headPic: "",
            licensePic: "",
            companyPic: "",
            privatePic: "",
            idPic: "",
            region: "",
            company: "",
            department: "",
            position: "",
            mobile: "",
            phone: "",
            mail: "",
            qq: "",
            wx: "",
            weibo: "",
            audit: "0"
        };
        ['handleSubmit'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/user/manage/getUserDetail",
            method: "post",
            data: {id: that.props.userID},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit() {
        let that = this;
        this.props.form.validateFields((err, values) => {
            that.setState({loading: true});
            ajax({
                url: "/user/manage/audit",
                method: "post",
                data: Object.assign({id: that.props.userID}, values),
                callback: function(data) {
                    if(data.status == 200) {
                        message.success("审核成功!", 1, function() {
                            history.back();
                        });
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            })
        })
    }

    return() {
        history.back();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form className="userDetailForm">
                    <FormItem
                        {...formItemLayout}
                        label="姓名"
                    >
                        <span className="ant-form-text">{this.state.name}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="性别"
                    >
                        <span className="ant-form-text">{this.state.gender == 0 ? "男" : "女"}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="昵称"
                    >
                        <span className="ant-form-text">{this.state.nickName}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="头像"
                    >
                        <span className="ant-form-text">
                            <img src={this.state.headPic} style={{width: "200px", height: "200px"}} />
                        </span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="营业执照"
                    >
                        <span className="ant-form-text">
                            <img src={this.state.licensePic} style={{width: "200px", height: "200px"}} />
                        </span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="公司名片"
                    >
                        <span className="ant-form-text">
                            <img src={this.state.companyPic} style={{width: "200px", height: "200px"}} />
                        </span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="个人照片"
                    >
                        <span className="ant-form-text">
                            <img src={this.state.privatePic} style={{width: "200px", height: "200px"}} />
                        </span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="身份证正面"
                    >
                        <span className="ant-form-text">
                            <img src={this.state.idPic} style={{width: "200px", height: "200px"}} />
                        </span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="地区"
                    >
                        <span className="ant-form-text">{this.state.region}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="公司"
                    >
                        <span className="ant-form-text">{this.state.company}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="部门"
                    >
                        <span className="ant-form-text">{this.state.department}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="职位"
                    >
                        <span className="ant-form-text">{this.state.position}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="手机"
                    >
                        <span className="ant-form-text">{this.state.mobile}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="固定电话"
                    >
                        <span className="ant-form-text">{this.state.phone}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="邮箱"
                    >
                        <span className="ant-form-text">{this.state.mail}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="QQ"
                    >
                        <span className="ant-form-text">{this.state.qq}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="微信"
                    >
                        <span className="ant-form-text">{this.state.wx}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="新浪微博"
                    >
                        <span className="ant-form-text">{this.state.weibo}</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="审核状态"
                     className={this.props.type=="detail" ? "hide" : "show"}>
                        {getFieldDecorator("audit", {
                            initialValue: this.state.audit.toString()
                        })(
                            <RadioGroup>
                                <Radio value="0">不通过</Radio>
                                <Radio value="1">通过</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        <Button className={this.props.type=="detail" ? "hide" : "btn-show"} icon="check" type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>审核</Button>
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDetailForm = Form.create({})(DetailForm);

module.exports = WrappedDetailForm;


